/* Base styles */
.motd-container {
    display: flex;
    height: 100%;
    background: #1e1e2e;
    color: #ffffff;
    font-family: 'Arial', sans-serif;
    overflow: hidden;
}

.patch-notes {
    flex: 3;
    padding: 1.25rem;
    /* ~20px */
    overflow-y: auto;
    border-right: 0.0625rem solid #3b3b4f;
    /* ~1px */
    background: #1e1e2e;
}

.patch-notes h2 {
    font-size: 1.8em;
    /* Unchanged, relative unit */
    margin-bottom: 0.9375rem;
    /* ~15px */
    color: #50fa7b;
    text-transform: uppercase;
}

.patch-notes-list {
    list-style: none;
    padding: 0;
}

.patch-item {
    margin-bottom: 1.25rem;
    /* ~20px */
}

.patch-item h3 {
    font-size: 1.3em;
    /* Unchanged, relative unit */
    color: #f1fa8c;
    margin-bottom: 0.625rem;
    /* ~10px */
}

.patch-item ul {
    list-style: disc;
    padding-left: 1.25rem;
    /* ~20px */
    font-size: 1em;
    /* Unchanged */
    color: #d0d0d0;
}

.patch-item li {
    margin-bottom: 0.3125rem;
    /* ~5px */
}

.sidebar {
    flex: 1;
    background: #2a2a3c;
    padding: 1.25rem;
    /* ~20px */
    display: flex;
    flex-direction: column;
}

.sidebar h2 {
    font-size: 1.5em;
    /* Unchanged, relative unit */
    margin-bottom: 0.9375rem;
    /* ~15px */
    color: #50fa7b;
    text-transform: uppercase;
}

.nav-links {
    display: flex;
    flex-direction: column;
    gap: 0.625rem;
    /* ~10px */
    background: none;
}

.nav-button {
    background: #3b3b4f;
    color: #ffffff;
    border: none;
    padding: 0.625rem;
    /* ~10px */
    font-size: 1em;
    /* Unchanged */
    cursor: pointer;
    border-radius: 0.3125rem;
    /* ~5px */
    transition: background 0.3s, transform 0.1s;
    text-align: left;
}

.nav-button:hover {
    background: #50fa7b;
    color: #1e1e2e;
    transform: translateX(0.3125rem);
    /* ~5px */
}

.nav-button:active {
    transform: translateX(0.125rem);
    /* ~2px */
}

/* Scrollbar styling for patch notes */
.patch-notes::-webkit-scrollbar {
    width: 0.5rem;
    /* ~8px */
}

.patch-notes::-webkit-scrollbar-track {
    background: #2a2a3c;
}

.patch-notes::-webkit-scrollbar-thumb {
    background: #50fa7b;
    border-radius: 0.25rem;
    /* ~4px */
}

.patch-notes::-webkit-scrollbar-thumb:hover {
    background: #f1fa8c;
}

/* Mobile media query */
@media (max-width: 767px) and (pointer: coarse) and (hover: none) {
    html {
        font-size: 16px;
        /* Base font size, adjust to 14px if needed */
    }

    .motd-container {
        flex-direction: column;
        /* Unchanged, stacks content */
    }

    .patch-notes {
        flex: none;
        /* Unchanged */
        border-right: none;
        /* Unchanged */
        border-bottom: 0.0625rem solid #3b3b4f;
        /* ~1px */
        height: auto;
        /* Unchanged */
        max-height: 60%;
        /* Reduced from 70vh to ensure sidebar visibility */
        padding: 0.625rem;
        /* ~10px, reduced for mobile */
    }

    .sidebar {
        flex: none;
        /* Unchanged */
        padding: 0.625rem 1.25rem;
        /* ~10px 20px, unchanged but in rem */
        flex-direction: row;
        /* Unchanged */
        overflow-x: auto;
        /* Unchanged */
        gap: 0.625rem;
        /* ~10px, unchanged but in rem */
        border-top: 0.0625rem solid #3b3b4f;
        /* ~1px */
        justify-content: space-between;
        /* Unchanged */
        min-height: 3rem;
        /* ~48px, ensures visibility */
        height: 50%;
    }

    .sidebar h2 {
        display: none;
        /* Unchanged, hides header */
    }

    .nav-links {
        display: flex;
        /* Explicitly set for clarity */
        flex-direction: row;
        /* Changed to row for wrapping */
        flex-wrap: wrap;
        /* Allow buttons to wrap into two columns */
        gap: 0.625rem;
        /* ~10px, unchanged */
        width: 100%;
        /* Unchanged, full width of sidebar */
        justify-content: space-between;
        /* Distribute buttons evenly */
        height: 20vh;
    }

    .nav-button {
        flex: 0 0 calc(50% - 0.3125rem);
        /* ~50% minus half gap for two columns */
        font-size: 1.5rem;
        /* ~14px, reduced from 1.2rem for better fit */
        padding: 0.75rem;
        /* ~12px, unchanged, ~44px tap target with font */
        text-align: center;
        /* Unchanged */
        white-space: nowrap;
        /* Unchanged */
        min-width: 0;
        /* Changed from 5rem to allow flexible sizing */
        box-sizing: border-box;
        /* Ensure padding is included in width */
    }

    .nav-button:hover {
        transform: none;
        /* Disable hover effects for mobile */
        box-shadow: none;
        /* Reset any base hover shadow */
    }
}

.webring-links {
    font-size: 0.85rem;
    color: #f1fa8c;
    text-align: center;
    border-bottom: 1px solid #3b3b4f;
}

.webring-links a {
    color: #8be9fd;
    text-decoration: none;
}

.webring-links a:hover {
    text-decoration: underline;
    color: #50fa7b;
}